.action-menu-btn {
  position: relative;
  width: 16px;
  height: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 20px;

  &::after {
    content: '\2807';
    font-size: 2rem;
    color: var(--gray-dark);
    position: absolute;
    top: calc(50% - 1.1rem);
    left: calc(50% - 0.5rem);
  }

}
