.footer {
    box-shadow: 0px -4px 16px 0px #373D5A14;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    padding: 8px 0;
    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        min-height: 28px;
    }

    &__count {
        flex-basis: 37.7%;
        background-color: var(--gray-normal);
        border: 1px solid var(--gray-regular);
        min-height: 28px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 4px;
    }

    &__btn {
        flex-basis: 61%;
    }

}

