html {
  box-sizing: border-box;
  background-color: aquamarine;
  font-size: 8px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

*:focus-visible {
  outline: 0;
}

body {
  height: 100%;
  font-family: var(--font-family);
  -webkit-text-size-adjust: 100%;
  margin: 0 auto;
  max-width: var(--content-width);
  min-height: 100%;
  font-weight: 400;
  border-radius: 8px;
  background-color: var(--bg-color);
  color: var(--text-color);
}


img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.btn-reset {
  border: none;
  outline: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  outline: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.container {
  max-width: var(--content-width);
  margin: 0 auto;
  padding: 0 var(--container-offset);
}

.title {
  font-size: 2rem;
  line-height: 2.5rem;
  font-weight: 500;
}

.text {
  font-size: 1rem;
  line-height: 1.25rem;
  color: var(--text-color);
  font-weight: 400;
}

.text-gray {
  font-size: 1rem;
  line-height: 1.25rem;
  color: var(--gray);
  font-weight: 400;
}

.text-fs10 {
  font-size: 1.25rem;
  line-height: 1.5rem;
  color: var(--text-color);
  font-weight: 500;
}

.text-fs12 {
  font-size: 1.5rem;
  line-height: 2rem;
  color: var(--text-color);
  font-weight: 500;
}

.text-descr {
  font-size: 1rem;
  line-height: 1.5rem;
  color: var(--text-color);
}
