.header__top {
  padding-top: 20px;
  padding-bottom: 6px;
  &-content {
    display: flex;
    justify-content: space-between;
  }

  &-left {
    display: flex;
    align-items: center;
  }

  &-right {
    display: flex;
    align-items: center;
  }

  &-logo {
    border-radius: 6px;
    border: 1px solid var(--blue);
    width: 28px;
    height: 28px;
    padding: 1px;
    margin-right: 8px;
  }

  &-img {
    border-radius: 3.34px;
  }
}
