.catalog {
    padding-top: 4px;
    &__title {
        margin-bottom: 4px;
    }

    &__box {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 8px;
    }

    &__scrollbar {
      max-height: 398px;
    }

    &__scrollbar .simplebar-track {
        right: -16px;
        background-color: var(--gray-scrollbar);
        border-radius: 24px;
    }
    &__scrollbar .simplebar-track.simplebar-vertical {
        width: 4px;
    }
    &__scrollbar .simplebar-scrollbar {
        background-color: var(--gray-very-dark);
        border-radius: 24px;
    }
}
